import React from 'react';
import Button from '@material-ui/core/Button';
import IconButton from '@material-ui/core/IconButton';
import AccountCircle from '@material-ui/icons/AccountCircle';
import MenuItem from '@material-ui/core/MenuItem';
import Menu from '@material-ui/core/Menu';
import Divider from '@material-ui/core/Divider';
import './css/header.css'
import logo from './img/logo.jpg'




export default class Header extends React.Component {
  state = {
    anchorEl: null
  }

  handleMenu = event => {
    this.setState({ anchorEl: event.currentTarget })
  }

  handleClose = event => {
    this.setState({ anchorEl: null })
  }

  render () {
    const auth = this.props.auth
    const { anchorEl } = this.state
    const open = Boolean(anchorEl);


    return (
      <header>
        <div className="logo">
          <img src={logo} alt="logo" height="45" />
          <div className="title">ЦВК КНУ</div>
        </div>
        {auth.loggedIn && (
                <div className="app-menu">
                  <IconButton
                    aria-owns={open ? 'menu-appbar' : null}
                    aria-haspopup="true"
                    onClick={this.handleMenu}
                    color="inherit"
                  >
                    <AccountCircle />
                  </IconButton>
                  <Menu
                    id="menu-appbar"
                    anchorEl={anchorEl}
                    anchorOrigin={{
                      vertical: 'top',
                      horizontal: 'right',
                    }}
                    transformOrigin={{
                      vertical: 'top',
                      horizontal: 'right',
                    }}
                    open={open}
                    onClose={this.handleClose}
                  >
                    <MenuItem disabled>{auth.user}</MenuItem>
                    <Divider />
                    <a href="https://elists-dev.herokuapp.com/admin/" target="_blank">
                      <MenuItem onClick={this.handleClose}>Адмін панель</MenuItem>
                    </a>
                    <a href="https://elists-dev.herokuapp.com/admin/logout">
                      <MenuItem onClick={this.handleClose}>Вийти</MenuItem>
                    </a>
                  </Menu>
                </div>
              )}
      </header>
    )
  }
}



